    z-index: 1100;
    height: 4.8rem;
    padding-right: 1rem;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
  }
}
footer small, footer .small {
  display: block;
}
footer small #resources-btn, footer .small #resources-btn {
  color: #4285f4;
  cursor: pointer;
}

.overlay {
  background-color: #ffffffaa;
  backdrop-filter: blur(8px);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  z-index: 2000;
  transition: 250ms ease-out;
  overflow-y: hidden;
}
.overlay .overlay-container {
  overflow-y: auto;
  height: 100%;
}
.overlay .overlay-container #close-btn {
  position: sticky;
  float: right;
  cursor: pointer;
}
@media (max-width: 575.98px) {
  .overlay .overlay-container #close-btn {
    top: 0.5rem;
    right: 0.5rem;
    padding: 0.5rem;
  }
}
@media (min-width: 768px) {
  .overlay .overlay-container #close-btn {
    top: 1rem;
    right: 1rem;
    padding: 0.5rem;
  }
}
.overlay .overlay-container #close-btn svg {
  fill: #000000;
}
@media (max-width: 575.98px) {
  .overlay .overlay-container #close-btn svg {
    width: 1.5rem;
    height: 1.5rem;
  }
}
@media (min-width: 768px) {
  .overlay .overlay-container #close-btn svg {
    width: 1.75rem;
    height: 1.75rem;
  }
}
.overlay .overlay-container #resources-list {
  padding: var(--section-vertical-spacing) 10vw;
}
.overlay .overlay-container #resources-list h2, .overlay .overlay-container #resources-list .h2 {
  margin-top: 3rem;
}
.overlay .overlay-container #resources-list h3, .overlay .overlay-container #resources-list .h3 {
  margin-top: 1.5rem;
}
.overlay .overlay-container #resources-list li {
  word-break: break-word;
  margin-bottom: 1rem;
}

/*Override PhotoSwipe top bar opacity*/
.pswp__ui--fit .pswp__top-bar,
.pswp__top-bar {
  background-color: rgba(0, 0, 0, 0);
}

/*Add 24dp shadow to lightbox image (same as material dialog)
  CSS from https://material-components.github.io/material-components-web-catalog/#/component/elevation */
.pswp--zoom-allowed .pswp__img,
.pswp__img {
  box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
}